import React, { useEffect, useState } from 'react'
import Layout from '../../components/Layout/Layout'
import AdminMenu from '../../components/Layout/AdminMenu'
import toast from 'react-hot-toast';
import axios from 'axios';
import { Select } from "antd"
import { useNavigate, useParams } from 'react-router-dom';
const { Option } = Select;

const UpdateProduct = () => {
    const navigate = useNavigate();
    const params = useParams();

    const [categories, setCategories] = useState([]);
    const [photo, setPhoto] = useState("");
    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    const [price, setPrice] = useState("");
    const [quantity, setQuantity] = useState("");
    const [category, setCategory] = useState("");
    const [shipping, setShipping] = useState("");
    const[id,setId] = useState("");
  
    // GET SINGLE PRODUCT
    const getSingleProduct = async() => {
        try {
            const {data} = await axios.get(`http://localhost:8080/api/v1/product/get-product/${params.slug}`)
            setName(data.product.name);
            setId(data.product._id);
            setCategory(data.product.category);
            setDescription(data.product.description);
            setPrice(data.product.price);
            setQuantity(data.product.quantity);
            setShipping(data.product.shipping);

            // if(data?.success){
            //     toast.success("Product updated successfully")
            // }
            // navigate("/dashboard/admin/products")

        } catch (error) {
            console.log(error);
            toast.error(`Something went wrong in getting single product`)
          }
        }

        useEffect(() => {
          getSingleProduct()
        },[])

           // Delete function
    const handleDelete = async() => {
      try {
        let answer = prompt("Are You Sure to delete this product....?");
        if(!answer) return;
        const {data} = await axios.delete(`http://localhost:8080/api/v1/product/delete-product/${id}`);
        if(data?.success){
          toast.success("Product Deleted successfully");
          navigate("/dashboard/admin/products")
        }
      } catch (error) {
        console.log(error);
        toast.error(`Something went wrong while deleting product`)
      }
    }
    
  
    // GET ALL CATEGORIES
    const getAllCategory = async () => {
      try {
        const { data } = await axios.get(`http://localhost:8080/api/v1/category/get-category`);
        if (data?.success) {
          setCategories(data?.category);
        }
      } catch (error) {
        console.log(error);
        toast.error(`Something went wrong in creating category`)
      }
    }
  
    // CREATE PRODUCT FUNC
    const handleUpdate = async(event) => {
      event.preventDefault();
      try {
        const productData = new FormData();
        productData.append("name",name);
        productData.append("description",description);
        productData.append("price",price);
        productData.append("quantity",quantity);
        photo && productData.append("photo",photo);
        productData.append("category",category);
  
        const {data} = await axios.put(`http://localhost:8080/api/v1/product/update-product/${id}`,productData)
  
        if(data?.success){
          toast.success("Product Updated Successfully");
          navigate('/dashboard/admin/products')
        }else{
          toast.error(data.message)
        }
      } catch (error) {
        console.log(error);
        toast.error(`Something went wrong in creating category`)
      }
    }

 
  
    useEffect(() => {
      getAllCategory();
    }, []);

return(
<>
<Layout>
        <div className="container-fluid m-3 p-3">

          <div className="row">
            <div className="col-md-3">
              <AdminMenu />
            </div>
            <div className="col-md-9">
              <h1>UPDATE PRODUCT</h1>
              <div className="m-1 w-75">
                <Select bordered={false}
                  placeholder="Select a category"
                  size='large'
                  showSearch
                  className='form-select mb-3'
                  onChange={(value) => { setCategory(value) }}
                  value={category.name}
                >
                  {categories?.map(c => (
                    <Option key={c._id} value={c._id}>
                      {c.name}
                    </Option>
                  ))}
                </Select>
                <div className="mb-3">
                  <label className='btn btn-outline-secondary col-md-12'>
                    {photo ? photo.name : "Upload Photo"}
                    <input type="file"
                      name='photo'
                      accept='image/*'
                      onChange={(e) => setPhoto(e.target.files[0])}
                      hidden
                    />
                  </label>
                </div>
                <div className="mb-3">
                  {photo ? (
                    <div className="text-center">
                      <img src={URL.createObjectURL(photo)} alt="product photo" height={'200px'}
                        className='img img-responsive'
                      />
                    </div>
                  ):(
                    <div className="text-center">
                      <img src={`http://localhost:8080/api/v1/product/product-photo/${id}`} alt="product photo" height={'200px'}
                        className='img img-responsive'
                      />
                    </div>
                  )
                  }
                </div>
            
                <div className="mb-3">
                  <input type="text"
                    value={name}
                    placeholder="Product Name"
                    className='form-control text-center'
                    onChange={(e) => setName(e.target.value)}
                  />
                </div>

                <div className="mb-3">
                  <textarea
                    type="text"
                    value={description}
                    placeholder="write a description"
                    className="form-control text-center"
                    onChange={(e) => setDescription(e.target.value)}
                  />
                </div>

                <div className="mb-3">
                  <input type="text"
                    value={price}
                    placeholder="Product Price"
                    className='form-control text-center'
                    onChange={(e) => setPrice(e.target.value)}
                  />
                </div>

                <div className="mb-3">
                  <input type="text"
                    value={quantity}
                    placeholder="Product Quantity"
                    className='form-control text-center'
                    onChange={(e) => setQuantity(e.target.value)}
                  />
                </div>

                <div className="mb-3">
                  <Select
                    bordered={false}
                    placeholder="Select Shipping "
                    size="large"
                    showSearch
                    className="form-select mb-3 text-center"
                    onChange={(value) => {
                      setShipping(value);
                    }}
                    value={shipping ? "Yes":"No"}
                  >
                    <Option value="0">No</Option>
                    <Option value="1">Yes</Option>
                  </Select>
                </div>
                
                <div className="mb-3">
                  <button className="btn btn-primary" onClick={handleUpdate}>UPDATE PRODUCT</button>
                  <button className="btn btn-danger m-3" onClick={handleDelete}>DELETE PRODUCT</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Layout>
</>
)}

export default UpdateProduct
