import React, { useEffect, useState } from 'react'
import Layout from '../components/Layout/Layout'
import { useParams } from 'react-router-dom';
import axios from 'axios';
import toast from 'react-hot-toast';

const ProductDetails = () => {

  const { slug } = useParams();

  const [product, setProduct] = useState({});

  const getProductDetails = async () => {
    try {
      const { data } = await axios.get(`http://localhost:8080/api/v1/product/get-product/${slug}`)

      if (data?.success) {
        setProduct(data?.product);
        console.log(data?.product)
      }
    } catch (error) {
      console.log(error)
      toast.error(error);
    }
  }

  useEffect(() => {
    getProductDetails();
  }, [])

  return (
    <Layout>
      <div className="row">
        <div className="col-md-4 w-40">
          <img src={`http://localhost:8080/api/v1/product/product-photo/${product._id}`} class="card-img-top" alt={product.name} style={{height:"60vh",marginTop:"4rem",width:"30vw",margin:"4rem"}} />
        </div>
        <div className="col-md-6 m-5 ml-5  w-60">
          {/* <h1 className=''>PRODUCT DETAILS</h1> */}
          <h5><span style={{fontSize:"2rem",color:"tomato",fontWeight:"bolder"}}>{product.name}</span></h5>
          <h5 style={{marginTop:"2rem"}}>Price:<span style={{fontSize:"1.3rem",color:"black",fontWeight:"bold"}}>Rs.{product.price}</span></h5>
          <h5 style={{marginTop:"2rem"}}>Category:<span>{product?.category?.name}</span></h5>
          <h5 style={{marginTop:"2rem"}}>Total Stock: <span style={{fontSize:"1.6rem",color:"red",fontWeight:"bolder"}}>{product.quantity}</span>  left..</h5>
          <h5 style={{marginTop:"2rem"}}>Description: <span>{product.description}</span></h5>
          <button style={{marginTop:"2rem",width:"100%"}} class="btn btn-primary ms-1">ADD TO CART</button>
        </div>
      </div>
    </Layout>
  )
}

export default ProductDetails
