import React, { useEffect, useState } from 'react'
import Layout from '../components/Layout/Layout'
import toast from 'react-hot-toast';
import axios from 'axios';
import { Checkbox, Radio } from "antd"
import { Prices } from '../components/Prices';
import { NavLink, useNavigate } from 'react-router-dom';
import { useCart } from '../context/cart';

const HomePage = () => {
  const navigate = useNavigate();

  const [products, setProducts] = useState([]);
  const [cart, setCart] = useCart();
  const [categories, setCategories] = useState([]);
  const [checked, setChecked] = useState([]);
  const [radio, setRadio] = useState([]);
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);




  // GET ALL CATEGORIES
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(`http://localhost:8080/api/v1/category/get-category`);
      if (data?.success) {
        setCategories(data?.category);
        console.log(data.category)
      }
    } catch (error) {
      console.log(error);
      toast.error(`Something went wrong in creating category`)
    }
  }



  // FILTER BY CATEGORY FUNCTION
  const handleFilter = (val, id) => {
    let all = [...checked];

    if (val) {
      all.push(id)
    } else {
      all.filter(c => c !== id)
    }
    setChecked(all);
  }


  useEffect(() => {
    getAllCategory();
    handleFilter();
  }, []);




  //    get all products
  const getAllProducts = async () => {
    try {
      setLoading(true);
      const { data } = await axios.get(`http://localhost:8080/api/v1/product/product-list/${page}`)
      setLoading(false);
      if (data?.success) {
        setProducts(data.products);
        // console.log(data)
      } else {
        toast.error("Unable to fetch products")
      }
    } catch (error) {
      setLoading(false)
      console.log(error);
      toast.error("Something went wrong");
    }
  }

  useEffect(() => {
    if (!checked.length || !radio.length) getAllProducts();
  }, [checked, radio]);

  useEffect(() => {
    if (checked.length || radio.length) filteredProduct()
  }, [checked, radio])




  // GET TOTAL COUNT
  const getTotal = async () => {
    try {
      const { data } = await axios.get(`http://localhost:8080/api/v1/product/product-count`);
      setTotal(data?.total)
    } catch (error) {
      console.log(error);
      toast.error(`Something went wrong in getting total products`)
    }
  }

  // Load More
  const loadMore = async () => {
    try {
      setLoading(true)
      const { data } = await axios.get(`http://localhost:8080/api/v1/product/product-list/${page}`)
      setLoading(false);
      setProducts([...products, ...data.products])
    } catch (error) {
      setLoading(false);
      console.log(error);
      toast.error(`Something went wrong in while pagination`)
    }
  }

  useEffect(() => {
    getTotal();
    if (page === 1) return;
    loadMore();
  }, [page]);



  // GET FILTERED PRODUCT
  const filteredProduct = async () => {
    try {
      const { data } = await axios.post(`http://localhost:8080/api/v1/product/product-filters`, {
        checked, radio
      })
      setProducts(data?.products)
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong");
    }
  }


  return (
    <Layout>
      <div className="row mt-3" style={{ minHeight: "100vh" }}>
        <div className="col-md-3 w-20" style={{ position: "sticky" }}>
          {/* Ctaegory filter */}
          <h3 className='ms-2'>Filter by Category</h3>
          <div className="d-flex flex-column m-3 size-2">
            {categories && categories.map((c) => (
              <Checkbox key={c._id} onChange={(e) => handleFilter(e.target.checked, c._id)}>
                {c.name}
              </Checkbox>
            ))}
          </div>

          {/* Price filter */}
          <h3 className='ms-2' style={{ marginLeft: "5rem" }}>Filter by Price</h3>
          <div className="d-flex flex-column m-3 size-2">
            <Radio.Group onChange={(e) => setRadio(e.target.value)}>
              {Prices && Prices.map(p => (
                <div key={p._id}>
                  <Radio value={p.array}>{p.name}</Radio>
                </div>
              ))}
            </Radio.Group>
          </div>

          {/* Clear filters */}
          <div className="d-flex flex-column m-3 size-2">
            <button className='btn btn-danger' onClick={() => window.location.reload()}>RESET FILTERS</button>
          </div>

        </div>
        <div className="col-md-9 w-75">
          {/* {JSON.stringify(radio, null, 4)} */}
          <h1 className='text-center' style={{ fontSize: "2.2rem", color: "black", fontWeight: "bolder", borderBottom: "2px solid black", width: "400px", margin: "auto" }}>ALL PRODUCTS</h1>
          <div className="d-flex flex-wrap">
            <div className="d-flex admin_route m-1 mt-3 flex-wrap">
              {products?.map(p => (
                <div className="card" style={{ width: "20rem", height: "30rem", marginTop: "1rem", marginLeft: "3rem", marginBottom: "1rem" }} >
                  <img src={`http://localhost:8080/api/v1/product/product-photo/${p._id}`} class="card-img-top" alt={p.name} style={{ height: "40vh", objectFit: "contain" }} />
                  <div class="card-body">
                    <h5 class="card-title text-center">{p.name}</h5>
                    <p class="card-text text-center" style={{ color: "tomato" }}>Rs:{p.price}</p>

                    <button class="btn btn-primary ms-1"
                      onClick={() => {
                        setCart([...cart, p])
                        localStorage.setItem('cart', JSON.stringify([...cart, p]))
                        toast.success("Product Added To Cart")
                      }}
                    >ADD TO CART</button>

                    
                    <button class="btn btn-secondary ms-1"
                      onClick={() => navigate(`/product/${p.slug}`)}
                    >More Details</button>
                  </div>
                </div>
              ))}

            </div>
            <div className='m-2 p-3 text-center'>
              {
                products && products.length < total && (
                  <button className='btn btn-warning'
                    onClick={(e) => {
                      e.preventDefault();
                      setPage(page + 1)
                    }}
                  >
                    {loading ? "Loading...." : "Load More..."}
                  </button>
                )
              }
            </div>
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default HomePage
