import React, { useEffect, useState } from 'react'
import Layout from '../../components/Layout/Layout'
import AdminMenu from '../../components/Layout/AdminMenu'
import toast from 'react-hot-toast';
import axios from 'axios';
import CategoryForm from '../../components/Form/CategoryForm';
import { Modal } from "antd"

const CraeteCategory = () => {
  const [categories, setCategories] = useState([]);
  const [name, setName] = useState("");
  const [visible, setVisible] = useState(false);
  const [selected, setSelected] = useState(null);
  const [updatedName, setUpdatedName] = useState("");



  // handle Form
  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const { data } = await axios.post(`http://localhost:8080/api/v1/category/create-category`, {
        name
      });

      if (data?.success) {
        toast.success(`${name} is created`);
        setName("")
        getAllCategory();
      } else {
        toast.error("Error in creating the category");
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong in creating category")
    }
  }



  // Update category
  const handleUpdate = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(`http://localhost:8080/api/v1/category/update-category/${selected._id}`, { name: updatedName });
      if (data.success) {
        toast.success(`${updatedName} is updated successfully`)
        setSelected("");
        setVisible(false);
        setUpdatedName("");
        getAllCategory();
      } else {
        toast.error(data.message)
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong")
    }
  }




  // DELETE CATEGORY
  const deleteHandler = async (id) => {
    try {
      const {data} = await axios.delete(`http://localhost:8080/api/v1/category/delete-category/${id}`);
      if(data.success){
        toast.success(`${name} category deleted successfully`);
        getAllCategory();
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong")
    }
  }




  // GET ALL CATEGORIES
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(`http://localhost:8080/api/v1/category/get-category`);
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      console.log(error);
      toast.error(`Something went wrong in creating category`)
    }
  }


  useEffect(() => {
    getAllCategory();
  }, []);



  return (
    <>
      <Layout>
        <div className="container-fluid m-3 p-3">

          <div className="row">
            <div className="col-md-3">
              <AdminMenu />
            </div>
            <div className="col-md-9">

              <h1 className='m-2'>Manage Category</h1>
              <div className="p-3 w-50">
                <CategoryForm handleSubmit={handleSubmit} value={name} setValue={setName} placeholder="Enter new category" />
              </div>
              <div className='w-75'>
                <table className="table">
                  <thead>
                    <tr>
                      <th scope="col">Name</th>
                      <th scope="col">Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {categories && categories.map(c => (
                      <>
                        <tr>
                          <td key={c._id}>{c.name}</td>
                          <td>
                            <button className='btn btn-primary ms-2'
                              onClick={() => {
                                setVisible(true);
                                setUpdatedName(c.name);
                                setSelected(c)
                              }}
                            >EDIT</button>
                            <button className='btn btn-danger ms-2'
                              onClick={() => { deleteHandler(c._id) }}
                            >DELETE</button>
                          </td>
                        </tr>
                      </>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
            <Modal onCancel={() => setVisible(false)} footer={null}
              visible={visible}
            >
              <CategoryForm value={updatedName} setValue={setUpdatedName} handleSubmit={handleUpdate}
                placeholder={updatedName}
              />
            </Modal>
          </div>
        </div>
      </Layout>
    </>
  )
}

export default CraeteCategory
