import React, { useState } from 'react'
import { NavLink, useNavigate } from 'react-router-dom'
import { useAuth } from '../../context/auth'
import toast from 'react-hot-toast';
import SearchInput from '../Form/SearchInput';
import { useCart } from '../../context/cart';


const Header = () => {
  const navigate = useNavigate();

  const [cart, setCart] = useCart();
  const [auth, setAuth] = useAuth();

  const handleLogOut = () => {
    setAuth({
      ...auth,
      user: null,
      token: '',
    })
    localStorage.removeItem('auth');
    toast.dismiss(`${auth.user.username} logged out successfully!`)
  }

  return (
    <>
      <nav className="navbar navbar-expand-lg bg-body-tertiary">
        <div className="container-fluid">
          <NavLink className="navbar-brand " id='main_div' to={"/"}>  SHOPPY </NavLink>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <SearchInput />
              <li className="nav-item">
                <NavLink className="nav-link active" aria-current="page" to={"/"} >Home</NavLink>
              </li>
              {/* <li className="nav-item">
                <NavLink className="nav-link active" aria-current="page" to={"/category"} >Categories</NavLink>
              </li> */}
              {!auth.user ? (
                <>
                  <li className="nav-item">
                    <NavLink className="nav-link" aria-current="page" to={"/register"}>Register</NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" aria-current="page" to={"/login"}>Login</NavLink>
                  </li>
                </>
              ) : (
                <>
                  <div className="dropdown">
                    <NavLink className="btn btn-secondary dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                      {auth?.user?.username}
                    </NavLink>
                    <ul className="dropdown-menu">
                      <li><NavLink className="dropdown-item"
                        to={`/dashboard/${auth?.user.role == 1 ? 'admin' : 'user'}`}
                      >Dashboard</NavLink></li>
                      <li className="nav-item">
                        <NavLink
                          onClick={handleLogOut}
                          className="dropdown-item"
                          aria-current="page"
                          to={"/login"}
                        >Logout</NavLink>
                      </li>
                    </ul>
                  </div>
                </>
              )}
              <li className="nav-item">
                <NavLink className="nav-link" aria-current="page" to={"/cart"}>({((cart?.length))})</NavLink>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  )
}

export default Header
