import React, { useEffect, useState } from 'react'
import AdminMenu from '../../components/Layout/AdminMenu'
import Layout from '../../components/Layout/Layout'
import toast from 'react-hot-toast';
import axios from 'axios';
import { NavLink } from 'react-router-dom';

const Products = () => {
    const [products, setProducts] = useState([]);

    //    get all products
    const getAllProducts = async () => {
        try {
            const { data } = await axios.get(`http://localhost:8080/api/v1/product/get-products`)
            if (data?.success) {
                setProducts(data.products);
                // console.log(data)
            } else {
                toast.error("Unable to fetch products")
            }
        } catch (error) {
            console.log(error);
            toast.error("Something went wrong");
        }
    }

    useEffect(() => {
        getAllProducts();
    }, []);

    return (
        <Layout>
            <div className="container-fluid m-3 p-3">
                <div className="row">
                    <div className="col-md-3">
                        <AdminMenu />
                    </div>
                    <div className="col-md-9">
                        <h1 className="text-center" style={{color:"black",fontSize:"2rem",fontWeight:"bolder",textTransform:"uppercase"}}>All Products List</h1>
                        <div className="d-flex admin_route m-1 mt-3 flex-wrap">
                            {products?.map(p => (
                                <NavLink to={`/dashboard/admin/product/${p.slug}`} key={p._id} className={"admin-link"}>
                                    <div class="card" style={{ width: "16rem",height:"25rem", marginTop:"3rem" }} >
                                        <img src={`http://localhost:8080/api/v1/product/product-photo/${p._id}`} class="card-img-top" alt={p.name}  style={{height:"40vh"}}/>
                                        <div class="card-body">
                                            <h5 class="card-title text-center">{p.name}</h5>
                                            <p class="card-text text-center" style={{color:"tomato"}}>Rs:{p.price}</p>
                                        </div>
                                    </div>
                                </NavLink>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default Products
