import React, { useEffect, useState } from 'react'
import Layout from '../../components/Layout/Layout'
import AdminMenu from '../../components/Layout/AdminMenu'
import axios from 'axios';
import {Select} from "antd"
const{Option} = Select;

const AdminOrders = () => {
   const[status,setStatus] = useState(["Not Process", "Processing", "Shipped", "deliverd", "cancel"]);
   const[changeStatus,setChangeStatus] = useState("");
   const [order, setOrder] = useState([]);

   const getOrders = async () => {
    try {
      const { data } = await axios.get(`http://localhost:8080/api/v1/auth/all-orders`)
      setOrder(data)
      console.log(data)
    } catch (error) {
      console.log(error)
    }
  }

  const handleChange = async(orderId,value) => {
    try {
        const {data} = await axios.put(`http://localhost:8080/api/v1/auth/order-status/${orderId}`,
        {status:value})
        getOrders()
    } catch (error) {
        console.log(error)
    }
  }

  useEffect(() => {
    getOrders();
  }, [])

    return (
        <Layout>
        <div className="container-fluid m-3 p-3">
            <div className="row">
                <div className="col-md-3">
                  <AdminMenu/>
                </div>
                <div className="col-md-9">
                    <h1 className="text-center">ALL ORDERS</h1>
                    {/* <h1 className="text-center">All Orders</h1> */}
                {order?.map((o,i) => {
                  return(
                    <div className="border shadow">
                    <table className="table">

                      <thead>
                        <tr>
                          <td scope='col'>#</td>
                          <td scope='col'>Status</td>
                          <td scope='col'>Buyer</td>
                          <td scope='col'>Date</td>
                          <td scope='col'>Payment</td>
                          <td>Quantity</td>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <th>{i+1}</th>
                          <th>
                            <Select bordered={false} onChange={(value) => handleChange(o._id,value)} 
                            defaultValue={o?.status}>
                             {status.map((s,i) => (
                                <Option key={i} value={s}>
                                {s}
                               </Option>
                             ))}
                            </Select>
                          </th>
                          <th>{o?.buyer.username}  </th>
                          <th>{o?.createdAt}  </th>
                          <th>{o?.payment.success ? "failed":"Success"}</th>
                          <th>{o?.products?.length} </th>
                        </tr>
                      </tbody>
                    </table>
                    <div className="container">
                    {o?.products?.map((p,i) => (
                        <div className="row container card flex-row mt-2 mb-2 p-2 cart_items">
                            <div className="col-md-4 cart_img">
                                <img src={`http://localhost:8080/api/v1/product/product-photo/${p._id}`} className="card-img-top cart_image " alt={p.name} style={{ height: "150px", objectFit: "contain", width: "150px", marginTop: "1rem", marginLeft: "3rem" }} />
                            </div>
                            <div className="col-md-8 cart_details">
                                <h4 style={{ color: "tomato", fontWeight: "bolder" }}>{p.name}</h4>
                                <p>{p.description.substring(0, 100)}......</p>
                                <h4>Price: <span className='price_span' style={{ fontSize: "1.9rem", fontWeight: "bold" }}>Rs.{p.price}</span></h4>
                                {/* <button className='btn btn-danger cart_button'
                                    onClick={() => removeCartItem(p._id)}
                                >Remove</button> */}
                            </div>
                        </div>
                    ))}
                    </div>
                    </div>
                  )
                })}
                </div>
            </div>
            </div>
        </Layout>
    )
}

export default AdminOrders
