import React, { useEffect, useState } from 'react'
import Layout from '../components/Layout/Layout'
import { useCart } from '../context/cart'
import { useAuth } from '../context/auth';
import { useNavigate } from 'react-router-dom';
import DropIn from "braintree-web-drop-in-react";
import axios from 'axios';
import toast from 'react-hot-toast';


const CartPage = () => {
    const navigate = useNavigate();

    const [cart, setCart] = useCart();
    const [auth, setAuth] = useAuth();
    const [clientToken, setClientToken] = useState("");
    const [loading, setLoading] = useState(false);
    const [instanse, setInstanse] = useState("");

    // TOTAL PRICE FUNCTION
    const totalPrice = () => {
        try {
            let total = 0;
            cart?.map(item => { total = total + item.price })
            return total;
        } catch (error) {
            console.log(error)
        }
    }



    // REMOVE ITEM FROM CART
    const removeCartItem = (pid) => {
        try {
            let myCart = [...cart];
            let index = myCart.findIndex(item => item._id === pid)
            myCart.splice(index, 1);
            localStorage.removeItem('cart')
            setCart(myCart);
        } catch (error) {
            console.log(error)
        }
    }

    // GET PAYMENT GATEWAY TOKEN
    const getToken = async () => {
        try {
            const { data } = await axios.get(`http://localhost:8080/api/v1/product/braintree/token`);
            setClientToken(data?.clientToken)
        } catch (error) {
            console.log(error)
        }
    }


    useEffect(() => {
        getToken();
    }, [auth?.token])

    // handle payment
    const handlePayment = async () => {
        try {
            setLoading(true)
            const { nonce } = await instanse.requestPaymentMethod();
            const { data } = await axios.post(`http://localhost:8080/api/v1/product/braintree/payment`, {
                nonce, cart
            })
            setLoading(false)
            localStorage.removeItem('cart')
            setCart([]);
            navigate("/dashboard/user/orders")
            toast.success("Payment completed successfully")
        } catch (error) {
            console.log(error)
            setLoading(false)
        }
    }

    return (
        <Layout>
            <div className="container container-fluid cart_div">
                <div className="row">
                    <div className="col-md-12">
                        <h1 className='text-center bg-light p-2 mb-1'>
                            {`Hello ${auth?.token && auth?.user?.username}`}
                        </h1>
                        <h4 className='text-center'>
                            {cart?.length > 0 ? `You have ${cart?.length} items in your cart ${auth?.token ? "" : "Please login to chekout"}` : "You Dont have any products"}
                        </h4>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-8">
                    {cart?.map(p => (
                        <div className="row container card flex-row mt-2 mb-2 p-2 cart_items">
                            <div className="col-md-4 cart_img">
                                <img src={`http://localhost:8080/api/v1/product/product-photo/${p._id}`} className="card-img-top cart_image " alt={p.name} style={{ height: "150px", objectFit: "contain", width: "150px", marginTop: "1rem", marginLeft: "3rem" }} />
                            </div>
                            <div className="col-md-8 cart_details">
                                <h4 style={{ color: "tomato", fontWeight: "bolder" }}>{p.name}</h4>
                                <p>{p.description.substring(0, 100)}......</p>
                                <h4>Price: <span className='price_span' style={{ fontSize: "1.9rem", fontWeight: "bold" }}>Rs.{p.price}</span></h4>
                                <button className='btn btn-danger cart_button'
                                    onClick={() => removeCartItem(p._id)}
                                >Remove</button>
                            </div>
                        </div>
                    ))}
                </div>
                <div className="col-md-4 text-center card bg-secondary bg-gradient">
                    <h2>Cart Summary</h2>
                    <p>Total || Checkout|| Payment</p>
                    <hr />
                    <h4>Total Price:Rs.{totalPrice()}</h4>
                    {auth?.user?.address ? (
                        <>
                            <div className="mb-3">
                                <h4>Current Addess</h4>
                                <h5>{auth?.user?.address} </h5>
                                <button className="btn btn-outline-warning"
                                    onClick={() => navigate("/dashboard/user/profile")}
                                >Update Address</button>
                            </div>
                        </>
                    ) : (
                        <div className="mb-3">
                            {
                                auth?.token ? (
                                    <button className='btn btn-outline-warning'
                                        onClick={() => navigate("/dashboard/user/profile")}
                                    >Update Address</button>
                                ) : (
                                    <button className='btn btn-danger'
                                        onClick={() => navigate("/login", {
                                            state: "/cart"
                                        })}
                                    >Please login to checkout</button>
                                )
                            }
                        </div>
                    )}
                    <div className="mt-3">
                        {
                            !clientToken || !cart?.length ? ("") :
                                <>
                                    <DropIn
                                        options={{
                                            authorization: clientToken,
                                            paypal: {
                                                flow: "vault",
                                            }
                                        }}
                                        onInstance={instanse => setInstanse(instanse)}
                                    />
                                </>
                        }

                        <button className='btn btn-primary'
                            onClick={handlePayment}
                            disabled={!clientToken || loading || !instanse || !auth?.user?.address}
                        >
                            {loading ? "Processing" : "Make Payment"}
                        </button>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default CartPage
