import React from 'react'
import Layout from '../components/Layout/Layout'
import { useSearch } from '../context/search'

const Search = () => {

    const [values, setValues] = useSearch();

    return (
        <Layout>
            <div className="container">
                <div className="text-center">
                    <h1>Search Results</h1>
                    <h4>{values?.results.length < 1 ? "No Products Found" : `Found ${values?.results.length}`}</h4>
                    <div className="d-flex flex-wrap mt-4">
                        <div className="d-flex admin_route m-1 mt-3 flex-wrap">
                            {values?.results.map((p) => (
                                <div className="card" style={{ width: "20rem", height: "30rem", marginTop: "1rem", marginLeft: "3rem", marginBottom: "1rem" }} >
                                    <img src={`http://localhost:8080/api/v1/product/product-photo/${p._id}`} class="card-img-top" alt={p.name} style={{ height: "40vh", objectFit: "contain" }} />
                                    <div class="card-body">
                                        <h5 class="card-title text-center">{p.name}</h5>
                                        <p class="card-text text-center" style={{ color: "tomato" }}>Rs:{p.price}</p>

                                        <button class="btn btn-primary ms-1">ADD TO CART</button>
                                        <button class="btn btn-secondary ms-1">More Details</button>
                                    </div>
                                </div>
                            ))}

                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default Search
